//import fetch from "node-fetch";
import config from "./config";
import convert from "../utils/convert";

export default class etherscan {
	static async getTxs() {
		let headers = new Headers({
			Accept: "application/json",
			"Content-Type": "application/json",
			"User-Agent":
				"Mozilla/5.0 (Macintosh; Intel Mac OS X 10_11_5) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/56.0.2924.87 Safari/537.36"
		});
		const url = `https://api.etherscan.io/api?module=account&action=txlist&address=${config.address()}&startblock=6604918&endblock=99999999&sort=asc&apikey=${config.apikey()}`;
		const txs = (await (await fetch(url, {
			method: "GET"
		})).json()).result;
		let numberOfBets = 0;
		let totalFunds = 0;
		for (var j = 0; j < txs.length; j++) {
			if (txs[j].isError == "1") {
				continue;
			}
			const v = parseFloat(convert.eth(txs[j].value));
			if (v > 0) {
				numberOfBets++;
			}
			totalFunds += v;
		}

		let usdFunds = totalFunds * (await config.usdrate());
		totalFunds = parseFloat(totalFunds.toFixed(1));
		usdFunds = parseFloat(usdFunds.toFixed(1));

		return { numberOfBets, totalFunds, usdFunds };
	}

	static async getLogs() {
		const url = `https://api.etherscan.io/api?module=logs&action=getLogs&fromBlock=6604918&toBlock=latest&address=${config.address()}&topic0=0x4ae4bd7655e0d350876a23cd90c4227b13db560e34435c6a488150a9c844bf5f&apikey=${config.apikey()}`;
		const txs = (await (await fetch(url, {
			method: "GET"
		})).json()).result;
		let allBets = [];
		for (var j = 0; j < txs.length; j++) {
			const d = web3.eth.abi.decodeLog(
				[
					{
						type: "address",
						name: "myString"
					},
					{
						type: "uint256",
						name: "eth" 
					},
					{
                        type: "uint256",
						name: "mySmallNumber" 
					}
				],
				txs[j].data
			);
			allBets.push(d);
		}

		return { allBets };
	}
}
